import * as types from "./actionTypes";
import * as questApi from "../../api/questApi";

export function loadMonstersSuccess(monsters) {
  return { type: types.LOAD_MONSTERS_SUCCESS, monsters };
}

export function loadMonsters() {
  return function(dispatch) {
    return questApi
      .getMonsters()
      .then(monsters => {
        dispatch(loadMonstersSuccess(monsters));
      })
      .catch(error => {
        throw error;
      });
  };
}

