import { combineReducers } from "redux";
import courses from "./courseReducer";
import authors from "./authorReducer";
import pokes from "./pokeReducer";
import monsters from "./monsterReducer";
import adjective from "./adjectiveReducer";
import author from "./authorReducer";
import course from "./courseReducer";
import emotion from "./emotionReducer";
import entryText from "./entryTextReducer";
import evilPlot from "./evilPlotReducer";
import groupName from "./groupNameReducer";
import lair from "./lairReducer";
import magicalObject from "./magicalObjectReducer";
import scenario from "./scenarioReducer";
import villain from "./villainReducer";
import action from "./actionReducer";
import person from "./personReducer";

const rootReducer = combineReducers({
  courses,
  pokes,
  authors,
  monsters,
  adjective,
  author,
  course,
  emotion,
  entryText,
  evilPlot,
  groupName,
  lair,
  magicalObject,
  scenario,
  villain,
  action,
  person
});

export default rootReducer;
